import { stopSubmit } from "redux-form";
import { getApi } from "../API/api";

const SET_AUTH_DATA = "SET_AUTH_DATA"
const GET_CAPTCHA = "GET_CAPTCHA"

const initialStateNav ={
  userId:null,
  email: null,
  login:null,
  isAuth:false,

   isFetching:false,
   captcha:null
}
 const authReducer = (state = initialStateNav, action) => {
   switch(action.type){
       case SET_AUTH_DATA:
         return  {
               ...state,
              ...action.data
               
           } 
      case GET_CAPTCHA : 
      return {...state, captcha:action.captcha}
           
    default: return state;

}

}


export const setAuthData = (userId, email, login, isAuth) => ({ type: SET_AUTH_DATA, data: { userId, email, login, isAuth }})
export const getCaptcha = (captcha) => ({type:GET_CAPTCHA, captcha})
export const getAuthMeThunk = () => {
   return async (dispatch) => {
      let data = await getApi.authMe()
         if (data.resultCode === 0) {
            let { id, email, login } = data.data
            dispatch(setAuthData(id, email, login, true))
         }
    return data

   }
}
export const onLoginThunk = (email, password, rememberMe, captcha) => {

  
   return async (dispatch) => {
    
     let response = await getApi.onLogin(email, password, rememberMe, captcha)
         if (response.data.resultCode === 0) {
            dispatch(getAuthMeThunk())
         }else{
            if (response.data.resultCode === 10){
               dispatch(getCaptchaThunk())
            }
            dispatch(stopSubmit("login", { _error: ` ${response.data.messages.length > 0 ? response.data.messages[0] : undefined}` }))
         }
  
   }
}

export const logoutThunk = () => {
   return async (dispatch) => {
     let response = await getApi.desLogin()
         if (response.data.resultCode === 0) {
            dispatch(setAuthData(null, null, null, false))
         }
      

   }
}
export const getCaptchaThunk = () => {
   return async (dispatch) => {
      let response = await getApi.getCaptcha()
      let captcha = response.data.url;
      dispatch(getCaptcha(captcha))

   }
}



export default authReducer;